* {
  margin: 0px;
  padding: 0px;
  background-color: cadetblue;
}
.main-div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 300px;
}
.child-div-0 h1 {
  background-color: white;
  padding: 9px;
  font-size: 27px;
}
figcaption {
  padding: 6px;
  margin: 5px 0px;
  text-align: center;
  color: darkred;
  font-weight: 500;
}
.additems input {
  width: 17em;
  margin: 12px;
  padding: 8px;
  border: 1px solid white;
}
.additems .icon {
  color: white;
  font-size: 20px;
  cursor: pointer;
}
.additems .icon:hover {
  color: black;
}
.each-list .icon {
  color: black;
  font-size: 20px;
  cursor: pointer;
}
.each-list .icon:hover {
  color: white;
}
.show-lists {
  padding: 5px;
  /* border: 2px solid white; */
}
.each-list h3 {
  position: absolute;
}
.each-list {
  padding: 6px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.btn button {
  padding: 3px;
  margin: 7px;
  color: white;
  background-color: darkred;
  font-weight: 900;
  cursor: pointer;
}
.btn button:hover {
  color: black;
}
.each-list li{
  padding: 5px 12px;
}
